import React from 'react';
import PropTypes from 'prop-types';

import {
	Panel,
	PanelHeader,
	Header,
	Button,
	Group,
	Cell,
	Div,
	Avatar,
	Tabbar,
	TabbarItem
} from '@vkontakte/vkui';

import { 
	Icon28HomeOutline,
	Icon28BillheadOutline,
	Icon28Menu,
	Icon28HistoryForwardOutline,
} from '@vkontakte/icons';

const Home = ({ id, go, fetchedUser }) => (
	<Panel id={id}>
		<PanelHeader>Главная</PanelHeader>

		<Banner
			before={<Icon28HistoryForwardOutline />}
			text="Новостей нет! Приходите позже!"
     	/>

		<Tabbar>
			<TabbarItem selected text="Главная">
				<Icon28HomeOutline />
			</TabbarItem>

			<TabbarItem onClick={go} data-to="work" text="ДЗ">
				<Icon28BillheadOutline />
			</TabbarItem>

			<TabbarItem onClick={go} data-to="more" text="Ещё">
				<Icon28Menu />
			</TabbarItem>
		</Tabbar>
	</Panel>
);

Home.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	fetchedUser: PropTypes.shape({
		photo_200: PropTypes.string,
		first_name: PropTypes.string,
		last_name: PropTypes.string,
		city: PropTypes.shape({
			title: PropTypes.string,
		}),
	}),
};

export default Home;
